<template>
    <section>
        <div class="card text-left">
          <div class="card-header">
            <h2>Chart</h2>
            <select class="form-control w-75" v-model="typeChart"> 
              <option value="bar">Bar</option>
              <option value="line">Line</option>
              <option value="doughnut">Doughnut</option>
              <option value="pie">Pie</option>
            </select>
          </div>
          <div class="card-body">
            <div>
              <canvas id="summaryBudgetChart"></canvas>
            </div>
          </div>
        </div>
    </section>
</template>

<script>
import Chart from 'chart.js/auto'
import { CHART_COLORS, namedColor } from '../utilities/Utils'

export default {
  data() {
      return {
        data:  {
          labels: ['Sum of Release Budget', 'Sum of Release Consumed (Actual)', 'Sum of Release Available'],
          datasets: []
        },
        typeChart: 'bar',
        chartContainer: null,
      }
    },
  props: [ 'dataChart', 'dataChartSelectedByFunctionalAreaDesc' ],
  computed: { },
  watch: {
    typeChart() {
      this.chartContainer.destroy()
      this.init()
    },
    dataChartSelectedByFunctionalAreaDesc(newValue) {
      this.chartContainer.destroy()
      this.data.datasets = []
      this.setDatasets(newValue)
      this.init()
    }
  },
  methods: {
    setDatasets(dataChart) {
      for (let index = 0; index < dataChart.length; index++ ){
          this.data.datasets[index] = {
                  label: dataChart[index].functionalAreaDesc,
                  borderColor: namedColor(index),
                  backgroundColor: namedColor(index),
                  data: [ dataChart[index].releaseBudget, dataChart[index].consumed, dataChart[index].releaseAvailable]
          }
      }
    }, 
    init() {
      var config = {
        type: this.typeChart,
        data: this.data,
        options: {
          plugins: {
              title: {
                display: true,
                text: this.dataChart[0].division
              },
              legend: {
                position: 'right'
              }
          }
        }
      }

      var ctx = document.getElementById('summaryBudgetChart')
      this.chartContainer = new Chart(ctx, config)
    }
  },
  mounted() {
    console.log('mounted chart')
    this.setDatasets(this.dataChart)
    this.init()
  }
}
</script>