<template>
  <header>
    <nav class="navbar navbar-expand-sm navbar-dark bg-danger">
      <a class="navbar-brand" href="#">FBP PAMASUKA</a>
      <button
        class="navbar-toggler d-lg-none"
        type="button"
        data-toggle="collapse"
        data-target="#collapsibleNavId"
        aria-controls="collapsibleNavId"
        aria-expanded="false"
        aria-label="Toggle navigation"
      ></button>
      <div class="collapse navbar-collapse" id="collapsibleNavId">
        <ul class="navbar-nav mr-auto mt-2 mt-lg-0">
          <li class="nav-item active">
            <a class="nav-link" href="#"
              >Home <span class="sr-only">(current)</span></a
            >
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#">Link</a>
          </li>
          <li class="nav-item dropdown">
            <a
              class="nav-link dropdown-toggle"
              href="#"
              id="dropdownId"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
              >Dropdown</a
            >
            <div class="dropdown-menu" aria-labelledby="dropdownId">
              <a class="dropdown-item" href="#">Action 1</a>
              <a class="dropdown-item" href="#">Action 2</a>
            </div>
          </li>
        </ul>
      </div>
    </nav>
  </header>
  <div class="container-fluid mt-2">
    <div class="row">
      <div class="col-3 pr-0">
        <Filter :filters="filters" @dataFilters="showFilters" />
      </div>
      <div class="col-9">
        <Chart
          v-if="dataTables.length"
          :dataChart="dataTablesNumberField"
          :dataChartSelectedByFunctionalAreaDesc="
            dataTablesNumberFieldByFunctionalAreaDesc
          "
        />
        <Table
          class="mt-2"
          v-if="dataTables.length"
          :dataTable="dataTablesNumberField"
          @functionalAreaDescParam="showFunctionalAreaDescParam"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Filter from "./components/Filter";
import Chart from "./components/Chart.vue";
import Table from "./components/Table.vue";

export default {
  name: "App",
  data() {
    return {
      dataTables: [],
      filters: [],
      selectedFilters: [],
      selectedfunctionalAreaDesc: "",
    };
  },
  components: { Filter, Chart, Table },
  methods: {
    showFilters(arrFilters) {
      this.selectedFilters = arrFilters;
    },
    showFunctionalAreaDescParam(parameter) {
      this.selectedfunctionalAreaDesc = parameter;
    },
  },
  computed: {
    dataTablesNumberField() {
      let datasets = [];
      for (let index = 0; index < this.dataTables.length; index++) {
        datasets[index] = {
          id: this.dataTables[index].id,
          division: this.dataTables[index].division,
          functionalAreaDesc: this.dataTables[index].functionalAreaDesc,
          releaseBudget: Number(this.dataTables[index].releaseBudget),
          consumed: Number(this.dataTables[index].consumed),
          releaseAvailable: Number(this.dataTables[index].releaseAvailable),
        };
      }

      return datasets;
    },
    dataTablesNumberFieldByFunctionalAreaDesc() {
      return this.dataTables.filter((element) => {
        return element.functionalAreaDesc == this.selectedfunctionalAreaDesc;
      });
    },
  },
  mounted() {
    console.log("app mounted");
    //datatable
    fetch("./data/data.json")
      .then((res) => res.json())
      .then((data) => (this.dataTables = data.data));

    fetch("./data/filters.json")
      .then((res) => res.json())
      .then((data) => (this.filters = data.filters));
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>