    exportTable() {
      console.log("export Table ...");
      const formatterOptions = {
        mime: "text/csv",
        fileKind: "csv",
        bom: true,
        columnDelimiter: ",",
        rowDelimiter: "\r\n",
        filename: "filename",
      };

      const grid = document.querySelector("revo-grid");
      //get plugin
      grid.getPlugins().then((plugins) => {
        plugins.forEach((p) => {
          if (p.exportFile) {
            const exportPlugin = p;

            exportPlugin.exportFile(formatterOptions);
          }
        });
      });

      console.log("fine");
    },

    functionalAreaDescParam(e) {
      if (this.functionalAreaDesc == e.target.innerText) {
        return;
      }
      // FIXME: Passing array as group data 'functionalAreaDesc' for enhanced chart group by that field
      // this.addDataToArray.push(e.target.innerText)
      this.$emit(
        "functionalAreaDescParam",
        (this.functionalAreaDesc = e.target.innerText)
      );
    },
